.logo {
  width: 24px;
  height: 24px;
  fill: var(--main-white-color);
}

.logo__text {
  color: var(--main-white-color);
  margin-left: 10px;
  font-size: 30px;
  line-height: 1.16px;
}

@media (max-width: 767.98px) {
  .logo__text {
    display: none;
  }
}

.navi {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 46px;
  display: flex;
}

@media (min-width: 768px) {
  .navi {
    margin-bottom: 43px;
  }
}

.page-link {
  align-items: center;
  display: flex;
}

.navi__list {
  display: flex;
}

.navi__list :not(:last-child) {
  margin-right: 40px;
}

.navi__link {
  font-size: var(--main-font-size);
  text-transform: uppercase;
  color: var(--main-white-color);
  transition: var(--transition);
  align-items: center;
  line-height: 1.16px;
  display: flex;
  position: relative;
}

.navi__link:hover, .navi__link:focus {
  color: var(--text-accent-color);
}

.current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--sec-red-color);
  position: absolute;
  bottom: -9px;
}

.search-container {
  max-width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form__input {
  width: 280px;
  height: 20px;
  color: var(--main-white-color);
  border: none;
  border-bottom: .5px solid var(--main-white-color);
  background-color: #0000;
  outline: none;
  align-items: center;
  padding-bottom: 6px;
  padding-right: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  display: flex;
}

@media (min-width: 768px) {
  .form__input {
    width: 336px;
  }
}

@media (min-width: 1280px) {
  .form__input {
    width: 396px;
  }
}

.form__input::placeholder {
  color: var(--main-white-color);
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
  display: flex;
}

.form {
  position: relative;
}

.form__icon {
  width: 12px;
  height: 12px;
  display: block;
}

.form__btn {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  outline: none;
  padding-bottom: 6px;
  position: absolute;
  top: 0;
  right: -1px;
}

.form__warning {
  width: 281px;
  text-align: left;
  color: var(--sec-red-color);
  margin-top: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  position: absolute;
}

@media (min-width: 768px) {
  .form__warning {
    width: 336px;
    text-align: center;
  }
}

@media (min-width: 1280px) {
  .form__warning {
    width: 394px;
  }
}

.lib__btns {
  justify-content: center;
  margin-top: 14px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

@media (min-width: 768px) {
  .lib__btns {
    margin-top: 8px;
  }
}

@media (min-width: 1280px) {
  .lib__btns {
    margin-top: 7px;
  }
}

.lib__btn {
  width: 130px;
  height: 44px;
  font-size: var(--main-font-size);
  line-height: var(--main-line-height);
  text-transform: uppercase;
  color: var(--main-white-color);
  cursor: pointer;
  border: 1px solid var(--main-white-color);
  transition: var(--transition);
  filter: drop-shadow(0 8px 43px #ff6b0199);
  background-color: #0000;
  border-radius: 5px;
  outline: none;
}

.lib__btn:focus, .lib__btn:hover {
  background-color: var(--btn-accent-color);
  border-color: var(--btn-accent-color);
}

@media (min-width: 768px) {
  .lib__btn {
    width: 152px;
  }
}

@media (min-width: 1280px) {
  .lib__btn {
    width: 148px;
    height: 42px;
  }
}

.lib__btn:not(:last-child) {
  margin-right: 20px;
}

@media (min-width: 768px) {
  .lib__btn:not(:last-child) {
    margin-right: 32px;
  }
}

@media (min-width: 1280px) {
  .lib__btn:not(:last-child) {
    margin-right: 16px;
  }
}

.active-btn {
  background-color: var(--btn-accent-color);
  border-color: var(--btn-accent-color);
}

