  background-color: #ffeb3b;
  background-image: radial-gradient(circle, #ffc107 15%, #ffeb3b 100%);
  background-repeat: no-repeat;
  border-radius: 50%;
  transition: transform .2s ease-in;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
